        name: decodeURIComponent("%E5%BA%86%E7%A5%9D%E5%8D%8E%E4%B8%BA5G%E4%B8%8A%E7%BA%BF") //庆祝华为5G上线
      },
      myRoute: "/land/face/",
      myPrice: "0",
      yuan: decodeURIComponent("%E5%85%83"),
      listNameLib: ["藏南","禁区宝贝","〆ᙆᴴᵁᴼ","Shaun","难得糊涂","黑的不是社会","ceci","lichj","黄小娇","立冏儿","后溪","阿龙","莆田汽车租赁","后梦霖溪","小白","漂泊的风","杨海山","冉冉","微小米", "刀子"],
