        },                 
      ]
    }
  },
  computed: {},
  mounted(){
    this.rMyPrice( 5, 80 )
    this.rListName( 15 )
    this.rListImgUrl( 15 )
    this.rListMoney( 5, 80 )
    this.rListDate()
    this.rListTime()
  },
  methods: {
    getArrayItems( arr, num) {
      var temp_array = [];
      for (var index in arr) {
          temp_array.push(arr[index]);
      }
      var return_array = new Array();
      for (var i = 0; i<num; i++) {
          if (temp_array.length>0) {
              var arrIndex = Math.floor(Math.random()*temp_array.length);
              return_array[i] = temp_array[arrIndex];
              temp_array.splice(arrIndex, 1);
          } else {
              break;
          }
      }
      return return_array;
    },  
    rMyPrice(min, max) {  
      this.myPrice = (Math.random() * (max - min) + min).toFixed(2);
    },     
    rListName( num ) {
      let len = this.getArrayItems(this.listNameLib, num)
      for(var i= 0 ;i< len.length; i++) {
        this.list[i].name = len[i]
      }
    },
    rListImgUrl( num ) {
      let len = this.getArrayItems(this.listImgUrlLib, num)
      for(var i= 0 ;i< len.length; i++) {
        this.list[i].imgUrl = this.$aliOss + this.myRoute + len[i]
      }
    },   
    rListMoney( min, max ) {
      for(var i= 0 ;i< this.list.length; i++) {
          this.list[i].money = ( Math.random() * (max - min) + min).toFixed(2)
      }
    }, 
    rListDate() {
      for(var i= 0 ;i< this.list.length; i++) {
          this.list[i].date = this.listDate
      }
    },    
    rListTime() {
      for(var i= 0 ;i< this.list.length; i++) {
          this.list[i].time = this.listTime[i]
      }
    },  
    handleToShareUrl() {
      getShareUrl().then(response => {
        let data = response.data
        window.location = data.url
      })            
    },
    goTousu(){
      this.$global.trackEvent( "落地页", "投诉", "我要投诉", "1");  
      this.$router.push({
         name:'tousuList'
      })
    }
  }
