  background:#f25543;
  width: 1600px;
  height: 1550px;
  margin: -1020px auto 0 -420px;
  border-radius: 1600px;
  text-align: center
}
.radius_box .con{
  width: 1600px;
  position: absolute;
  bottom: 130px;
  text-align: center;
  color: #ffdfb1;
}
.radius_box .f_title{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px
}
.radius_box .f_title img{
  width: 50px;
  height: 50px;  
  margin-right: 10px;
  border-radius: 10px;
}
.radius_box .price{
  margin: 60px 0 30px 0;
}
.radius_box .price span{
  font-size: 100px;
  font-weight: bold
}
.radius_box .price em{
  font-size: 32px
}
.radius_box .btn_get{
  display: block;
  background: #ffe2b1;
  width: 300px;
  height: 80px;
  line-height: 80px;
  margin: 0 auto;
  border-radius: 40px;
  color: #f15543;
  font-size: 40px
}

.hd_top .replay{
  margin: 40px 0 0 0;
  text-align: center;
  color: #d5a75a;
  font-size: 20px
}
.hd_top .replay .face{
  background: url("https://red01.oss-cn-hongkong.aliyuncs.com/land/img/face.png") no-repeat center center;
  width: 80px;
  height: 80px;
  border-radius: 20px;
  margin: 0 auto 20px auto;
  border: 1px dashed #d5a75a;
  padding: 40px;
}

.list_box{
  background: #fff;
  border-top: 10px solid #eeeef3;
  padding: 20px;
}
.list_box .total{
  color: #999;
}
.list_box ul{
  padding: 20px 0 0 0;
}
.list_box li{
  display: flex;
  padding: 20px 0; 
  border-top: 1px solid #eee;
}
.list_box .ly_lt{
  margin: 0 20px 0 0
}
.list_box .ly_lt img{
   width: 90px;
   height: 90px;
   border-radius: 10px;
}
.list_box .ly_rt{
  width: 100%;
  color: #000
}
.list_box .ly_rt .box{
  display: flex;
  justify-content: space-between
}
.list_box .ly_rt .date{
  color: #999;
  margin: 20px 0 0 0
}
.list_box .tips{
  font-size: 24px;
  color: #999;
  text-align: center;
  margin: 20px 0 0 0
}
.ft{
  text-align: center;
  color: #999;
  padding: 40px 0;
}
.list_box .ts{
  padding: 10px 0;
  margin-top: 10px;
  color: #666699;
  font-size: 30px;
}

.animation {
	-webkit-animation: bounce 5s infinite ease;
	animation: bounce 5s infinite ease;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
}
@keyframes bounce {
    from, 20%, 53%, 80%, to {
      animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
      transform: translate3d(0,0,0);
    }

    40%, 43% {
      animation-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      transform: translate3d(0, -8px, 0);
    }

    70% {
      animation-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      transform: translate3d(0, -2px, 0);
    }

    90% {
      transform: translate3d(0,-4px,0);
    }
}
</style>



